import React from 'react';
import { X, MapPin, Calendar, Tag, Activity } from 'lucide-react';
import { SelectedFeature } from '../types';

interface FeaturePopupProps {
  feature: SelectedFeature | null;
  onClose: () => void;
}

const FeaturePopup: React.FC<FeaturePopupProps> = ({ feature, onClose }) => {
  if (!feature) return null;

  // Helper to choose an icon based on property keys (basic heuristics)
  const getIconForProp = (key: string) => {
    const k = key.toLowerCase();
    if (k.includes('date') || k.includes('time')) return <Calendar size={12} />;
    if (k.includes('id') || k.includes('code')) return <Tag size={12} />;
    if (k.includes('stat') || k.includes('val')) return <Activity size={12} />;
    return <div className="w-1 h-1 rounded-full bg-primary-500" />;
  };

  return (
    <div className="absolute right-0 top-0 bottom-0 w-80 bg-slate-900/95 backdrop-blur-md border-l border-slate-700 shadow-2xl z-30 flex flex-col animate-in slide-in-from-right duration-300">
      {/* Header */}
      <div className="p-5 border-b border-slate-700 bg-slate-800/50 flex justify-between items-start">
        <div>
          <div className="flex items-center gap-2 text-primary-400 mb-1">
            <MapPin size={16} />
            <span className="text-xs font-bold tracking-wider uppercase">Feature Details</span>
          </div>
          <h2 className="text-lg font-bold text-white font-mono truncate max-w-[200px]">
            {feature.properties['name'] || feature.properties['NAME'] || `Feature #${feature.id}`}
          </h2>
          <span className="text-xs text-slate-500 bg-slate-800 px-2 py-0.5 rounded border border-slate-700 mt-2 inline-block">
            {feature.geometryType}
          </span>
        </div>
        <button 
          onClick={onClose}
          className="p-1 hover:bg-slate-700 rounded text-slate-400 hover:text-white transition-colors"
        >
          <X size={20} />
        </button>
      </div>

      {/* Content */}
      <div className="flex-1 overflow-y-auto p-0">
        <table className="w-full text-left border-collapse">
            <tbody>
                {Object.entries(feature.properties).map(([key, value], index) => (
                    <tr key={key} className={`border-b border-slate-800 ${index % 2 === 0 ? 'bg-slate-900/30' : 'bg-transparent'}`}>
                        <td className="py-3 px-5 w-1/3 align-top">
                            <div className="flex items-center gap-2 text-xs font-semibold text-slate-500 uppercase">
                                {getIconForProp(key)}
                                {key}
                            </div>
                        </td>
                        <td className="py-3 px-5 text-sm text-slate-300 font-mono break-all">
                            {typeof value === 'object' ? JSON.stringify(value) : String(value)}
                        </td>
                    </tr>
                ))}
            </tbody>
        </table>
        
        {Object.keys(feature.properties).length === 0 && (
            <div className="p-8 text-center text-slate-500">
                <p>No properties available for this feature.</p>
            </div>
        )}
      </div>

      {/* Footer Actions */}
      <div className="p-4 border-t border-slate-700 bg-slate-900 grid grid-cols-2 gap-3">
        <button className="px-4 py-2 bg-slate-800 hover:bg-slate-700 text-slate-300 text-xs font-bold rounded border border-slate-600 transition-colors">
            ZOOM TO
        </button>
        <button className="px-4 py-2 bg-primary-600 hover:bg-primary-500 text-white text-xs font-bold rounded shadow-lg shadow-primary-900/20 transition-colors">
            EXPORT
        </button>
      </div>
    </div>
  );
};

export default FeaturePopup;
